html {

    // override
    .MuiDialog-paper {
        max-width: 800px;
    }
    .MuiDialog-root .MuiBackdrop-root {
        background-color: rgba(0, 0, 0, 0.3);
    }
    
    .lds-modal {
        // min-height: 400px; // causeing padding issues at bottom
        height: auto; 
        padding: 16px;
        width: 600px; // default
        border-radius: 8px;

        // modifiers
        &.is-size-sm {width: 400px;}
        &.is-size-lg {width: 800px;}

        // members
        &-top {
            padding-bottom: 16px;
            height: 38px;
            width: 100%;
        }

        &-closer {
            position: absolute;
            top: 16px;
            left: auto;
            right: 16px;
            width: 22px;
            height: 22px;
            cursor: pointer;
        }
        &-bottom {
            padding-top: 16px;
            width: 100%;
            text-align: right;
        }

        &-content .lds-alert {
            margin-bottom: 30px;
        }
    }




} // HTML