
.lds-topnav {
    position: fixed;
    top: 0;
    left: 0;
    z-index: 10;
    height: 72px;
    width: 100%;
    min-width: 1300px; // --- WIP
    background: repeat-x url(../../images/toolbars/topnav-bg.png);
    background-size: 20px 72px;


    &-logo {
        display: inline-block;
        position: relative;
        top: 0;
        left: 0;
        width: 90px;
        height: 72px;
        vertical-align: top;

        i {
            position: absolute;
            top: 10px;
            left: 20px;
        }
    }

    &-right {
        position: absolute;
        top: 0;
        left: auto;
        right: 0;
        background: top right url(../../images/toolbars/topnav.png);
        background-size: 1440px 72px;
        width: 480px;
        height: 72px;
        > * {
            position: absolute;
            top: 0;
            left: 0;
        }
    }

    &-tab {
        display: inline-block;
        height: 72px;
        padding: 24px 16px 0;
        color: #CEECFF;
        cursor: pointer;
        text-decoration: none;

        &.is-selected {
            background-color: #0093E8;
            color: white;
        }
    }

    &-number {
        display: inline-block;
        border-radius: 11px;
        height: 22px;
        min-width: 22px;
        font-size: 12px;
        line-height: 18px;
        color: #374D58;
        background-color: #FFC854;
        text-align: center;
        padding: 2px 4px 0;
        margin-left: 10px;
    }
}
