html {


.lds-button {
    display: inline-block;
    text-align: center;
    height: 44px;
    padding: 10px 16px;
    min-width: 120px;
    height: 44px;
    color: #374D58;
    background: white;
    border: 1px solid #BCC8CE;
    border-radius: 6px;
    cursor: pointer;

    // fonts
    font-family: 'Lato';

    .lds-icon {
        position: relative;
        top: 3px;
        margin-right: 8px;
    }


    // modifiers
    &.is-type-primary {
        color: white;
        background: #0093E8;
        border-width: 0;
    }

    &.is-width-auto {
        min-width: none;
        width: auto;
    }
}

.lds-buttongroup {
    > * {
        margin-left: 12px;
    }

    .lds-button.is-name-delete {
        float: left; 
        margin-left: 0; 
        border-width: 0; 
        padding-left: 0;
        &:hover {color: red; }
    }
}


} // HTML