html {

    #lds-tasks-container {
        padding: 0 24px 0 294px;
    }


    #lds-tasks {
        background: 0 101px white repeat-x url(../../images/general/table-bg.png); 
        background-size: 100px 1276px; 
        width: 100%; 
        height: 761px;

        .lds-spriteleft,
        .lds-spriteright {
            background: repeat-x url(../../images/tasks/tasks-list.png); 
            background-size: 754px 761px; 
            width: 580px; 
            height: 761px;
        }
        .lds-spriteright {
            background-position: top right;
            width: 170px;
            min-width: 170px; // make it stick
        }
    }

    #lds-tasks-sidenav {
        background: repeat-x url(../../images/tasks/tasks-sidenav.png); 
        background-size: 270px 952px; 
        height: 800px;
    }

    #lds-tasks-edit-modal {
        background: url(../../images/tasks/tasks-edit-modal.png); 
        background-size: 768px 1165px; 
        height: 1165px;
    }




    // --- grid --- //

    #lds-tasks-grid {
        background: 0 0 no-repeat url(../../images/tasks/tasks-grid.png); 
        background-size: 990px 591px; 
        width: 990px; 
        height: 591px;
    }


    
    

}