


.lds-layout {
    display: inline-block;
    text-align: center;
    height: 44px;
    padding: 12px 16px;
    min-width: 120px;
    height: 44px;
    color: #374D58;
    background: white;
    border: 1px solid #BCC8CE;
    border-radius: 6px;
    cursor: pointer;

    // fonts
    font-family: 'Lato';


    // modifiers
    &.is-type-primary {
        color: white;
        background: #0093E8;
        border-width: 0;
    }

    &.is-width-auto {
        min-width: none;
        width: auto;
    }

    &-top {
        height: 72px;
    }
}


// --- 3col --- //

.lds-layout-3col {
    width: 100%;
    height: 100%;

    > table {
        width: 100%;
        height: 100%;;
    }


    .lds-layout-left {
        width: 270px;
        background-color: #F5F9FD;
    }

    .lds-layout-main {
        width: auto;
        padding-left: 24px;
        padding-right: 24px;
    }

    .lds-layout-right {
        width: 368px;
        padding-left: 24px;
        border-left: #E1EAEF 1px solid;
    }
}


