html {


    .lds-menu {
        white-space: nowrap;
        cursor: pointer;
    }


    ul.lds-menulist {
        min-width: 200px;
        padding: 8px 0;
        border-radius: 4px;
    
    }

    li.lds-menuitem {
        position: relative;
        top: 0;
        left: 0;
        padding: 6px 16px 0;
        font-size: 14px;
        line-height: 13px;
        height: 32px;
        cursor: pointer;
        white-space: nowrap;

        /* Text sm/Regular */
        font-family: 'Lato';
        font-style: normal;
        font-weight: 400;
        font-size: 14px;
        line-height: 20px;
    }
    
    .lds-menuitem {
    
    
        &-icon {
            position: absolute;
            top: 8px;
            left: 16px;
        }

        &-icon-right {
            position: absolute;
            top: 8px;
            left: auto;
            right: 16px;
        }

        // modifiers
        &.is-selected {
            color: #0093E8;
        }

        &.is-iconic {
            padding-left: 44px;
        }

        &:hover {
            background-color: #F5F9FD;
        }

        
    }
    
    
    
    } // HTML