html {



    // --- CHECKBOX --- //

    .lds-checkbox {
        position: relative;
        top: 0;
        left: 0;
        display: inline-block;
        cursor: pointer;
        padding: 0px 0 0 28px;   
        min-height: 20px;
        
        .lds-icon {
            position: absolute;
            top: 0;
            left: 0;
        }
        
        // modifier
        &.is-notlabeled {padding: 0 0 0 20px; }

        &.is-size-sm {
            min-height: 16px;
            &.is-notlabeled {padding: 0 0 0 16px; }
        }
        &.is-size-lg {
            min-height: 24px;
            &.is-notlabeled {padding: 0 0 0 24px; }
        }

        &.is-sprite {
            // bordere
        }
    }


    // --- INPUT --- //

    .lds-input {
        position: relative;
        top: 0;
        left: 0;
        display: inline-block;
        text-align: center;
        width: 100%;
        height: 58px;
        color: #374D58;
        background: white;
        border: 1px solid #BCC8CE;
        border-radius: 6px;
        cursor: text;

        // members
        &-contianer {
            width: 220px;
            position: relative;
            top: 0;
            left: 0;
        }

        &-label {
            display: none;
            position: absolute;
            top: 0;
            left: 0;
            padding: 9px 14px 0;
            width: 100%;
            text-overflow: ellipsis;
            overflow: hidden;
            text-align: left;
            white-space: nowrap;
        }

        &-hint {
            display: flex;
            margin-top: 6px;
            align-items: center;
            color: #748995;
            gap: 4px;

            &.is-type-error {
                color: #D00000;
            }

            &.is-type-success {
                color: #2A9B02;
            }
        }

        &-field {
            display: block;
            width: 100%;
            height: 100%;
            padding: 12px 14px 12px 14px;
            background-color: white;
            border-width: 0;
            text-overflow: ellipsis;
            overflow: hidden;
            text-align: left;
            border-radius: 6px;

            &::placeholder {
                color: #748995;
            }

            &:focus {
                background-color: white;
            }
        }

        &-icon {
            position: absolute;
            left: 14px;
            top: 19px;
        }

        &-icon-right {
            position: absolute;
            left: auto;
            right: 14px;
            top: 19px;
        }

        // filled
        &.is-filled {
            .lds-input-label {
                display: block;
            }
    
            .lds-input-field {
                padding: 27px 14px 12px 14px;
                // TODO
            }
        }

        // iconic
        &.is-iconic {
            .lds-input-label,
            .lds-input-field {
                padding-left: 47px !important;
            }
        }
        &.is-iconic-right {
            .lds-input-label,
            .lds-input-field {
                padding-right: 47px !important;
            }
        }

        // disabled
        &.is-disabled .lds-input-field {
            background-color: #F5F9FD;
        }

        // select
        &.is-type-select > * {
            cursor: pointer;
        }

        // textarea
        &.is-type-textarea {
            height: 124px;
        }


        // error
        &.is-error {
            border-color: #D00000;
        }

        // modifiers

    }


    // --- SELECT --- //

    .lds-select {
        // display: inline-block;
        white-space: nowrap;
        cursor: pointer;
        display: flex;
        align-items: center;

        // text 
        &-text {
            display: flex;
            align-items: center;
            cursor: pointer;
        }
    }

        
    ul.lds-selectlist {
        min-width: 200px;
        padding: 8px 0;
        border-radius: 4px;

    }

    li.lds-selectitem {
        padding: 2px 4px;
        font-size: 14px;
        line-height: 13px;
        height: 32px;

        /* Text sm/Regular */
        font-family: 'Lato';
        font-style: normal;
        font-weight: 400;
        font-size: 14px;
        line-height: 20px;

        &.is-selected {
            color: #0093E8;
        }
    }



} // HTML