html {


    .lds-sidenav {
        display: flex;
        background: repeat-y url(../../images/general/sidenav-bg.png);
        background-size: 270px 0;
        // height: 400px;

        &-left {
            background: no-repeat url(../../images/general/sidenav.png);
            background-size: 810px 952px;
            background-position: 0 0;
            $width: 270px; // width
            width: $width;
            min-width: $width;
            max-width: $width;
            background-color: #F5F9FD;
        }

        &-right {
            width: 100%;
            padding: 0 24px;
            overflow-x: hidden;
            overflow-y: auto;
        }

        &-tabs {
            display: flex;
            > * {
                height: 60px;
            }
        }


        // types
        &.is-type-tasks-grid .lds-sidenav-left {
            background-position: -270px 0;
        }

        &.is-type-tasks-auto .lds-sidenav-left {
            background-position: -540px 0;
        }
    }

    
} // html