html {

    // loader
    .lds-loader {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        z-index: 2;
        background-color: rgba(255, 255, 255, 0.5) !important;
        /// transition-duration: .3s !important; // TEMP


        // spinning circle
        .MuiCircularProgress-svg {
            color: #27A4ED;
        }

        &.is-color-ltgray {
            background-color: #F5F9FD;
        }

    }
    
    // loader background
    .lds-loader-bg {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background-color: white;
        z-index: 1;

        &.is-color-ltgray {
            background-color: #F5F9FD !important;
        }
    }


} // END HTML