html {

    #lds-dashboard2 {
        display: flex;
        // flex-direction: column;
        // gap: 16px;
        // justify-content: flex-start;
        // justify-content: space-between;
        padding: 32px;
        gap: 24px;
        

        &-left {
            width: 100%;

            > * {
                margin-bottom: 16px;
            }
        }
        &-right {
            position: relative;
            top: 0;
            left: 0;
            background: -1079px -94px url(../../images/dashboard/dashboard.png); 
            background-size: 1440px 1091px; 
            width: 340px;
            min-width: 340px;
            height: 980px;
        }

        &-bottom {
            display: flex;
            justify-content: space-between;
            gap: 20px;
            &-box {
                width: 50%;
            }
        }
    }

    // --- dashboard tasks --- //

    #lds-dashboard-alert {
        background-color: #FFF7DB;


        .lds-spriteleft,
        .lds-spriteright {
            background: 0 0 repeat-x url(../../images/dashboard/dashboard-alert.png); 
            background-size: 1033px 90px; 
            width: 800px; 
            height: 90px;
        }
        .lds-spriteright {
            background-position: top right;
            width: 130px;
            min-width: 130px; // make it stick
        }
    }

    #lds-dashboard-tasks {
        background: 0 36px white repeat-x url(../../images/general/table-bg.png); 
        background-size: 100px 1276px; 
        width: 100%; 
        height: 333px;

        .lds-spriteleft,
        .lds-spriteright {
            background: 0 0 repeat-x url(../../images/dashboard/dashboard-tasks.png); 
            background-size: 1033px 333px; 
            width: 830px; 
            height: 333px;
        }
        .lds-spriteright {
            background-position: top right;
            width: 170px;
            min-width: 170px; // make it stick
        }
    }

    #lds-dashboard-messages {
        background: 0 48px white repeat-x url(../../images/dashboard/dashboard-messages-bg.png); 
        background-size: 200px 400px; 
        width: 100%; 
        height: 470px;

        .lds-spriteleft,
        .lds-spriteright {
            background: 0 0 repeat-x url(../../images/dashboard/dashboard-messages.png); 
            background-size: 505px 500px; 
            width: 460px; 
            height: 500px;
        }
        .lds-spriteright {
            background-position: top right;
            width: 40px;
            min-width: 40px; // make it stick
        }
    }


