

#lds-app-meds-tabs {
    position: absolute;
    top: 108px;
    left: 0;
    width: 100%;
    height: 60px;
    z-index: 100;
    background: url(../../images/app/meds/meds-tabs.png); 
    background-size: 375px 180px; 

    &-inner {
        display: flex;
        width: 100%;
        height: 100%;
    }

    .lds-spritebutton {
        width: 33.33%;
        height: 100%;
    }

    &.is-tab-home {background-position: 0 0;}
    &.is-tab-orders {background-position: 0 -60px;}
    &.is-tab-reminders {background-position: 0 -120px;}

    // position: absolute;
